footer{
    background-color:  var(--dGreen);
    bottom: 0 !important;
    padding: 50px 0;
    text-align: center;
    margin-top: 100px;
}
.footer-logo{
    height: 75px;
    margin-bottom: 40px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    cursor: pointer;
    -webkit-transition: .6s;
    -o-transition: .6s;
    transition: .6s;
}

.footer-logo:hover{
    -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
}

.marginb0{
    margin-bottom: 0px !important;
}
.hr-footer{
    border: solid 1px var(--white);
    max-width: 70%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
}
.footer-info{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    color: var(--white);
    margin: 10px 0;
}
.footer-info p{
    font-size: 1.2rem;
    margin-bottom: 0px !important;
}


