.product-card{
    max-width: 300px;
    overflow: hidden;
    height: 460px;
    margin: 25px 0;
    -webkit-transition: .6s;
    -o-transition: .6s;
    transition: .6s;
    position: relative;
    left: 50% !important;
    -webkit-transform: translateX(-50%) !important;
        -ms-transform: translateX(-50%) !important;
            transform: translateX(-50%) !important;
    background-color: var(--white);
    -webkit-box-shadow: 0 0 30px var(--gShadow);
            box-shadow: 0 0 30px var(--gShadow);
    transition: .6s;
    border-radius: 20px;
}
.product-card img{
    width: 100%;
    height: 300px;
    -o-object-fit: contain;
       object-fit: contain;
    display: block;
}
.product-box h2{
    width: 100%;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    text-transform: capitalize;
    cursor: pointer;
    color: var(--black);
    margin-top: 10px;
}
.product-box .price{
    color: var(--green);
    font-size: 1.3rem;
}
.row_btn{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    position: absolute;
    bottom: 0;
}
.row_btn a{
    letter-spacing: 2px;
    font-weight: bold;
    width: 50%;
    text-transform: uppercase;
    text-decoration: none;
    color: var(--white);
    padding: 10px;
}
#btn_view:hover{
    background-color: var(--white);
    color: var(--black);
    -webkit-transition: .6s;
    -o-transition: .6s;
    transition: .6s;
}
#btn_buy:hover{
    background-color: var(--white);
    color: var(--gShadow);
    -webkit-transition: .6s;
    -o-transition: .6s;
    transition: .6s;   
}
#btn_view{
    background-color: var(--black);
    height: 60px;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    display: -ms-flexbox;
    display: flex;
    font-size: 1.1rem;
    -webkit-transition: .6s;
    -o-transition: .6s;
    transition: .6s;
}
#btn_buy{
    background-color: var(--gShadow);
    height: 60px;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    display: -ms-flexbox;
    display: flex;
    font-size: 1.1rem;
    -webkit-transition: .6s;
    -o-transition: .6s;
    transition: .6s;
}
.product-card input{
    position: absolute !important;
    width: 30px;
    height: 30px;
    left: 0;
}


