.categ{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    background-color: var(--white);
    margin: 20px 0;
    padding: 25px 0;
    text-align: center;
    border-radius: 20px;
}
.btn-edit{
    color: var(--gShadow) !important;
    background-color: var(--white) !important;
    border: 0;
}
.btn-delete{
    background-color: var(--white) !important;
    border: 0;
}
.border-radius{
    border-radius: 20px;
}


